import { connect } from 'react-redux';
import { areaOfTensionSteel, providedSteel } from '../area-tension-steel/area-tension-steel.component';
import { distributionSteel, providedDistSteel } from '../distribution-steel/distribution-steel.component';
import { required_d } from '../effective-depth/effective-depth.component'
import './results.styles.scss'

let Results = ({inputData, designLoads, ast, distSteel})=>{
    let id = {...inputData, ...designLoads};
    let ida = {...inputData, ...designLoads, ...ast};
    let idd = {...inputData, ...designLoads, ...distSteel};
    const {d} = inputData;

    let depthStatus = d > required_d(id);
    let ast_Status = providedSteel(ast) > areaOfTensionSteel(ida);

    let distSteelStatus = providedDistSteel(distSteel) > distributionSteel(idd)
    return(
        <div className="results">
            <StatusButton status={depthStatus} success="Deptyh Ok" error="Increase Depth"/>
            <StatusButton status={ast_Status} success="AST ok" error="Increase AST steel"/>
            <StatusButton status={distSteelStatus} success="Distribution steel ok" error="Increase Distribution Steel"/>
        </div>
    )
}

let StatusButton = ({status, success, error})=>{
    return(
        <span className={`status-button ${status?"success":"error"}`}>{status?success:error}</span>
    )
}

let mapStateToProps = (state)=>({
    inputData: state.inputData,
    designLoads: state.designLoads,
    ast: state.ast,
    distSteel: state.distSteel
})

export default connect(mapStateToProps)(Results)